<template>
  <Static>
    <v-row align="center" justify="center">
      <div class="signin">
        <div class="login-container flex-it">
          <div class="login-content">
            <v-form @submit.prevent="signin">
              <span class="red--text px-1" v-if="emailError">{{
                emailError
              }}</span>
              <v-text-field
                class="mt-2"
                type="email"
                v-model.trim="email"
                placeholder="Username"
                required
                solo
                hint="example@gmail.com"
                v-on:keyup.enter="signin"
              ></v-text-field>
              <span class="red--text" v-if="passwordError">{{
                passwordError
              }}</span>
              <v-text-field
                class="mt-2"
                type="password"
                v-model.trim="password"
                placeholder="Password"
                required
                solo
                hint="Please use strong password"
                v-on:keyup.enter="signin"
              ></v-text-field>
            </v-form>
            <div class="flex-it mt-5">
              <v-btn
                rounded
                color="#610018"
                @click="signin"
                class="btn-width"
                dark
                >Sign In</v-btn
              >
            </div>
            <div class="flex-it mt-15">
              <span>Don't have an account ?</span>
              <v-btn disabled type="submit" @click="signup" color="green" text
                >contact admin</v-btn
              >
            </div>
          </div>
        </div>
      </div>
    </v-row>
  </Static>
</template>
<script>
import Static from '../layouts/Static'
import { au } from '@/firebase/init'
export default {
  name: 'Signin',
  data: () => ({
    email: '',
    password: '',
    emailError: '',
    passwordError: '',
  }),
  components: {
    Static,
  },
  methods: {
    signin() {
      if (!this.email) {
        this.emailError = 'Please enter an email'
      }
      if (this.email) {
        this.emailError = ''
      }
      if (!this.password) {
        this.passwordError = 'Please enter a password'
      }
      if (this.password) {
        this.passwordError = ''
      }
      // Firebase user sign-in
      if (this.email && this.password) {
        au.signInWithEmailAndPassword(this.email, this.password)
          .then(() => {
            this.$router.push({ name: 'Admin' })
          })
          .catch((err) => {
            this.emailError = err.message
          })
        this.emailError = null
      } else {
        this.emailError = 'Please check your email and password'
      }
      this.$gtag.event('signin-button-click', {
        event_category: 'documentation',
        event_label: 'Signin Button Clicked',
        value: 1,
      })
    },

    signup() {
      this.$router.replace({ name: 'Signup' })
    },
  },
}
</script>
